import React, { useEffect, useRef, useState } from 'react';
import './ARview.css'
import '../components/Aframe-components'
import data from '../data/data_animals.json'

import ARInfomationModal from '../components/ARInformationModal';
import ARInstructionsModal from '../components/ARInstructionsModal';

const ARview = () => {
    const sceneRef = useRef(null);
    const videoRef = useRef(null);
    const targetRefs = useRef({});
    const [arReady, setArReady] = useState(false);
    const [targetFounded, setTargetFounded] = useState(false);
    const audioRefs = useRef({});
    const [isPlaying, setIsPlaying] = useState(false);
    const [currentTarget, setCurrentTarget] = useState(0);

    const [openModalInformation, setOpenModalInformation] = useState(false);

    const [rotationY, setRotationY] = useState(0);
  

    const [modelScale, setModelScale] = useState(2); // Escala inicial
    const handleScaleChange = (event) => {
        setModelScale(event.target.value);
    };

    const handleButtonClick = () => {
        if (audioRefs.current[currentTarget]) {
            if (isPlaying) {
              audioRefs.current[currentTarget].pause(); // Pausa el audio
            } else {
              audioRefs.current[currentTarget].play();  // Reproduce el audio
            }
            setIsPlaying(!isPlaying);
          }
      };

      useEffect(() => {
        // Cargar los archivos de audio correspondientes a cada target
        audioRefs.current[0] = new Audio(process.env.PUBLIC_URL + '/Assets/sounds/mastodonte.mp3');
        audioRefs.current[1] = new Audio(process.env.PUBLIC_URL + '/Assets/sounds/fox.mp3');
        audioRefs.current[3] = new Audio(process.env.PUBLIC_URL + '/Assets/sounds/deer.mp3');
        audioRefs.current[4] = new Audio(process.env.PUBLIC_URL + '/Assets/sounds/boar.mp3');
        audioRefs.current[2] = new Audio(process.env.PUBLIC_URL + '/Assets/sounds/sloth.mp3');
    
        return () => {
            // Limpiar los audios cuando el componente se desmonta
            Object.values(audioRefs.current).forEach(audio => {
                audio.pause();
                audio.src = "";
            });
        };
    }, []);

    const onTargetFound = (index) => {
        setTargetFounded(true);
        setCurrentTarget(index);
        console.log("TargetFound")
    }

    const onTargetLost = (index) => {
        setTargetFounded(false);

        audioRefs.current[index].pause();
        setIsPlaying(false);
    }

    const repositionVideo = () => {
        const video = videoRef.current;
        const sceneEl = sceneRef.current;

        sceneEl.resize();
        if (video) {
            setTimeout(() => {
                video.style.left = 0;
                video.style.top = 0;
                video.style.objectFit = "cover";

                video.style.width = '100%';
                video.style.height = '100%';
            }, 100);

        }
    }

    useEffect(() => {
        const sceneEl = sceneRef.current;
        let arSystem = null;

        const handleRenderStart = () => {
            // Valido que exista información del ave para consultarla
            //getDataInformation()

            arSystem = sceneEl.systems["mindar-image-system"];
            arSystem.start();
        }

        const handleARReady = (event) => {
            videoRef.current = document.querySelector('video');
            repositionVideo();
            setArReady(true);
        }

        const targets = [0, 1, 2, 3, 4]; // Lista de índices de targets

        targets.forEach((index) => {
            
            const targetEl = targetRefs.current[index];
            
            if (targetEl) {
                targetEl.addEventListener("targetFound", () => onTargetFound(index));
                targetEl.addEventListener("targetLost", () => onTargetLost(index));
            }
        });

        sceneEl.addEventListener('renderstart', handleRenderStart);
        sceneEl.addEventListener('arReady', handleARReady);
        window.addEventListener("resize", repositionVideo);

        return () => {

            targets.forEach((index) => {
                const targetEl = targetRefs.current[index];
                
                if (targetEl) {
                    targetEl.removeEventListener("targetFound", () => onTargetFound(index));
                    targetEl.removeEventListener("targetLost", () => onTargetLost(index));
                }
            });


            if (arSystem) {
                arSystem.stop();
            }
            sceneEl.removeEventListener('renderstart', handleRenderStart);
            sceneEl.removeEventListener('arReady', handleARReady);
            window.removeEventListener("resize", repositionVideo);
        };
    }, []);

    // Manejador para la rotación con deslizamiento
    const handleMouseDown = (event) => {
        let startX = event.clientX || event.touches[0].clientX;

        const handleMouseMove = (moveEvent) => {
            let currentX = moveEvent.clientX || moveEvent.touches[0].clientX;
            let deltaX = currentX - startX;
            setRotationY(prevRotationY => prevRotationY + deltaX * 0.1); // Ajustar la sensibilidad de rotación
            startX = currentX;
        };

        const handleMouseUp = () => {
            window.removeEventListener('mousemove', handleMouseMove);
            window.removeEventListener('mouseup', handleMouseUp);
            window.removeEventListener('touchmove', handleMouseMove);
            window.removeEventListener('touchend', handleMouseUp);
        };

        window.addEventListener('mousemove', handleMouseMove);
        window.addEventListener('mouseup', handleMouseUp);
        window.addEventListener('touchmove', handleMouseMove);
        window.addEventListener('touchend', handleMouseUp);
    };

    useEffect(() => {
        window.addEventListener('mousedown', handleMouseDown);
        window.addEventListener('touchstart', handleMouseDown);

        return () => {
            window.removeEventListener('mousedown', handleMouseDown);
            window.removeEventListener('touchstart', handleMouseDown);
        };
    }, []);

    //https://cdn.jsdelivr.net/gh/hiukim/mind-ar-js@1.2.5/examples/image-tracking/assets/card-example/card.mind
    return (
        <div style={{ width: "100%", height: "100%", position: "fixed", top: 0, left: 0 }} className='w-full h-full fixed'>

                    <div className='logo-container'>
                        <img src={process.env.PUBLIC_URL + "/Assets/logos/gigantes-logo.JPG"}/>
                    </div>
            {
                targetFounded &&
                <>
                    <div className="slider-container">
                        <input
                            type="range"
                            min="1"
                            max="3"
                            step="0.1"
                            value={modelScale}
                            onChange={handleScaleChange}
                        />
                        <p>Escala: {modelScale}</p>
                    </div>

                    <div className='button-container'>
                        <button className="sound-button" onClick={handleButtonClick}>
                            <i className="fas fa-volume-up"></i>
                        </button>
                    </div>
                </>
            }
            {arReady && !openModalInformation && <ARInstructionsModal 
                open={!targetFounded}
                text="Buscando huellas..."
            />}

            {
                arReady &&
                <ARInfomationModal
                    open={openModalInformation}
                    visibleButton={targetFounded}
                    setOpen={setOpenModalInformation}
                    title={data.animals[currentTarget].nombre}
                    subtitle={data.animals[currentTarget].nombre_c}
                    image={null}
                    description={data.animals[currentTarget].descripcion}
                />
            }
            
            


            <a-scene ref={sceneRef} 
            inspector-url="-"
            mindar-image={"imageTargetSrc:" + process.env.PUBLIC_URL + "/Assets/targets/targets.mind" + ";  autoStart: false; uiLoading: no; uiError: no; uiScanning: yes; filterMinCF: 0.0001; filterBeta:0.01; warmupTolerance:1; missTolerance:3"} 
            color-space="sRGB" embedded renderer="colorManagement: true, physicallyCorrectLights" 
            vr-mode-ui="enabled: false" 
            device-orientation-permission-ui="enabled: false"
            /* shadow="type: pcfsoft" */>
                <a-assets>
                    <img id="card" src="https://cdn.jsdelivr.net/gh/hiukim/mind-ar-js@1.2.0/examples/image-tracking/assets/card-example/card.png" />
                    <a-asset-item id="avatarModel" src="https://cdn.jsdelivr.net/gh/hiukim/mind-ar-js@1.2.0/examples/image-tracking/assets/card-example/softmind/scene.gltf"></a-asset-item>

                </a-assets>







                <a-camera position="0 0 0" look-controls="enabled: false"></a-camera>

                {/* <a-entity ref={targetRef} mindar-image-target="targetIndex: 0">
                    <a-plane src="#card" position="0 0 0" height="0.552" width="1" rotation="0 0 0"></a-plane>
                    <a-gltf-model rotation="0 0 0 " position="0 0 0.1" scale="0.005 0.005 0.005" src="#avatarModel" animation="property: position; to: 0 0.1 0.1; dur: 1000; easing: easeInOutQuad; loop: true; dir: alternate"></a-gltf-model>
                </a-entity> */}


                <a-entity ref={el => (targetRefs.current[0] = el)} id="targets0" mindar-image-target="targetIndex: 0" visible="true">
                    <a-gltf-model 
                    rotation={`0 ${rotationY} 0`} // 180 -90 -90
                    scale={`${modelScale} ${modelScale} ${modelScale}`}
                    visible="true" 
                    position="0 0 0" 
                    src={process.env.PUBLIC_URL + "/Assets/models/elephant1.glb"} 
                    apply-texture="textureUrl: eleph.png"
                    animation-mixer="clip: Elephant_Idle_Final_88F; crossFadeDuration:0.2; loop: repeat"
                    /* shadow="cast: true" */ 
                    ></a-gltf-model>

                   {/*  <a-plane 
                    rotation="0 0 0" 
                    width="5" 
                    height="5" 
                    shadow="receive: true" 
                    material="shader: shadow; opacity: 1.0"
                    >
                    </a-plane>

                    <a-entity light="type:directional; castShadow:true;" intensity="1" position="2 2 10" target="#targets1"></a-entity>
                <a-entity light="type: ambient; color: #BBB; intensity:0.1"></a-entity> */}
                    
                </a-entity>

                <a-entity ref={el => (targetRefs.current[3] = el)} id="targets3" mindar-image-target="targetIndex: 3" visible="true">
                    <a-gltf-model 
                    rotation={`0 ${rotationY} 0`} // -90 -90 -90
                    scale={`${modelScale*0.25} ${modelScale*0.25} ${modelScale*0.25}`}
                    visible="true" 
                    position="0 0 0" 
                    src={process.env.PUBLIC_URL + "/Assets/models/deer1.glb"} 
                    apply-texture="textureUrl: Deer.png" 
                    animation-mixer="clip: Deer_Idle_Final_120F.001; crossFadeDuration:0.2; loop: repeat"
                    /* shadow="cast: true" */ 
                    ></a-gltf-model>
                    
                </a-entity>

                <a-entity ref={el => (targetRefs.current[1] = el)} id="targets1" mindar-image-target="targetIndex: 1" visible="true">
                    <a-gltf-model 
                    rotation={`0 ${rotationY} 0`} 
                    scale={`${modelScale} ${modelScale} ${modelScale}`}
                    visible="true" 
                    position="0 0 0" 
                    src={process.env.PUBLIC_URL + "/Assets/models/fox1.glb"}  
                    animation-mixer="clip: Fox_Idle; crossFadeDuration:0.2; loop: repeat"
                    /* shadow="cast: true" */ 
                    ></a-gltf-model>
                    
                </a-entity>

                <a-entity ref={el => (targetRefs.current[4] = el)} id="targets4" mindar-image-target="targetIndex: 4" visible="true">
                    <a-gltf-model 
                    rotation={`0 ${rotationY} 0`} 
                    scale={`${modelScale*0.6} ${modelScale*0.6} ${modelScale*0.6}`}
                    visible="true" 
                    position="0 0 0" 
                    src={process.env.PUBLIC_URL + "/Assets/models/boar1.glb"}  
                    animation-mixer="clip: Idle_4; crossFadeDuration:0.2; loop: repeat"
                    /* shadow="cast: true" */ 
                    ></a-gltf-model>
                    
                </a-entity>

                <a-entity ref={el => (targetRefs.current[2] = el)} id="targets2" mindar-image-target="targetIndex: 2" visible="true">
                    <a-entity rotation={`0 ${rotationY} 0`}>
                        <a-gltf-model 
                        rotation="0 0 0"
                        scale={`${modelScale*0.15} ${modelScale*0.15} ${modelScale*0.15}`}
                        visible="true" 
                        position="0 0 1.5" //0 2 0
                        src={process.env.PUBLIC_URL + "/Assets/models/sloth1.glb"}  
                        animation-mixer="clip: Sloth_Idle2_Final_112; crossFadeDuration:0.2; loop: repeat"
                        /* shadow="cast: true" */ 
                        ></a-gltf-model>
                    </a-entity>
                    
                    
                </a-entity>

                
                

            </a-scene>
        </div>

    )
}

export default ARview