

window.AFRAME.registerComponent('apply-texture', {
    schema: {
      textureUrl: { type: 'string' }
    },
    init: function () {
      const data = this.data;
      const el = this.el;
  
      
      el.addEventListener('model-loaded', function (event) {
        const object3D = event.detail.model;
        
        
        const textureLoader = new window.THREE.TextureLoader();
        const texture = textureLoader.load(process.env.PUBLIC_URL + "/Assets/models/"+ data.textureUrl);
        texture.flipY = false;
        texture.encoding = window.THREE.sRGBEncoding;
        
        object3D.traverse(function (child) {
          if (child.isMesh) {
            child.material = new window.THREE.MeshStandardMaterial({
                map: texture,           
                metalness: 0.3,
                roughness: 0.7,
                color: 0xffffff,
                envMapIntensity: 1
              });
            child.material.needsUpdate = true; // Asegura que se actualice la textura
          }
        });
      });
    }
  });